<template>
	<div>
		<h3>所有任务</h3>
		<ul>
			<li v-for="item in dataList" :key="item.id">
				<input type="checkbox" v-model="$store.getters.isShow[item.id]" @change="modifySelect(item.id, item.isShow[item.id])" /> 
				<span :class="{boxStyle: item.isShow}">{{ item.text }}</span>
			</li>
			<p>{{ $store.state.isShow }}</p>
		</ul>
	</div>
</template>

<script>
import { mapState } from 'vuex';
export default {
	name: 'allTask',
	computed: {
		...mapState(['dataList'])
	},
	methods: {
		modifySelect (id, val) {
			this.$store.commit("modifySelected", { id, isShow: val })
		}
	}
};
</script>

<style scoped>
.boxStyle {
	color: #999;
	text-decoration: line-through;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}
li {
	font-size: 20px
}
</style>
